<template>
  <div class="shopcart">
    <navigation-bar @onLeftClick="goToBack" pageName="购物车" >
      <template v-slot:nav-right>
        <div class="rightTitle" :class="{active:bottomBar==1}" @click="toManageCart">编辑</div>
        <div class="rightTitle" :class="{active:bottomBar==2}" @click="allReady">完成</div>
      </template>
    </navigation-bar>
    <!--显示购物车-->
    <!-- <van-pull-refresh v-model="isLoading" @refresh="onRefresh"> -->
      <div class="cartContent" ref="cartContent">
        <!--店铺-->
        <div class="goodsCard" v-for="(item, i) in lists">
          <div class="shopCard">
            <img :src="item.selectFlag==0?img.beforClick:img.afterclick" v-show="bottomBar==2" @click="checkShop(i)">
            <img :src="item.checkedShop==false?img.beforClick:img.afterclick" v-show="bottomBar==1" @click="checkShop(i)">
            <div :class="themeCurrent == '1' ? 'shopName-l' : 'shopName'" @click="toShop(item.shopCode)">{{item.shopName}}</div>
            <img class="shopSelect" :src="img.arrowShop">
          </div>
          <van-swipe-cell :disabled="bottomBar==1?true:false" class="goodsDes" v-for="(message, index) in item.skuList">

            <div class="secksellBox" v-if="message.seckillFlag==0||message.seckillFlag==1||message.seckillFlag==4">
                <div class="secksellMsgBox">
                    <div class="secksellTitle">秒杀</div>
                    <div class="secksellMsg" v-if="message.seckillFlag==0||message.seckillFlag==4">该商品距离秒杀结束仅剩</div>
                    <div class="secksellMsg" v-if="message.seckillFlag==1">该商品距离秒杀开始还有</div>
                    <!-- <div class="secksellTime"></div> -->
                    <van-count-down  :auto-start="true" class="secksellTime" :time="message.time" @finish="countDownFinish" />
                </div>
                <div class="secksellMoreBox" @click="goSeckSellList(message)">
                    <div class="secksellMore">去逛逛</div>
                    <img class="secksellMoreImg" src="@/assets/images/home/property_payment/more_red.png" alt="">
                </div>
            </div>

            <div class="goodsDetailBox">
                <div class="selectBox">
                    <img class="selectBtn" :src="message.selectFlag==0?img.beforClick:img.afterclick" v-show="bottomBar==2"
                      @click="checkSku(i,index)">
                    <img class="selectBtn" :src="message.checkedSku==false?img.beforClick:img.afterclick" v-show="bottomBar==1"
                      @click="checkSku(i,index)">
                  </div>
                  <div class="imgBox"><img class="goodsPic" :src="message.skuPicUrl" :onerror="defaultImg" @click="turnToGoods(message)">
                  <div class="lowStock"
                      v-if="message.storeNumber<=message.warningValue&&message.storeNumber!=0&&message.spuStatus!='02'&&message.seckillFlag==3">仅剩{{message.storeNumber}}件</div>
                      <span class="noStore" v-if="message.storeNumber==0&&message.spuStatus!='02'&&message.seckillFlag==3">补货中</span>
                      <span class="noStore" v-if="message.spuStatus=='02'">已下架</span>
                  </div>

                  <div class="goodsDetails">
                    <div :class="themeCurrent == '1' ? 'goodsName-l' : 'goodsName'" @click="turnToGoods(message)">{{message.spuName}}</div>
                    <div :class="themeCurrent == '1' ? 'attrValueContent-l' : 'attrValueContent'" @click="turnToGoods(message)">{{message.attrValueContent}}</div>
                    <div class="goodsDetailBottom">

                      <div :class="themeCurrent == '1' ? 'skuPrice-l' : 'skuPrice'" @click="turnToGoods(message)">￥{{message.skuPrice}}</div>
                        <div class="stepperBox">
                            <img class="subtractBtn" src="../../assets/images/mall/mall_icon_subtract.png" alt="">
                            <img class="addBtn" src="../../assets/images/mall/icon_add_cut.png" alt="">
                            <van-stepper class="stepperq" v-model="message.skuCount" @blur="getCount(message)"
                                         @overlimit='addNumBtn(message,item.shopCode)' @plus='plusBtn(message)' @minus='minusBtn(message)' :max="message.skuCountLimit" :class="{stepperThemeL:themeCurrent=='1'}"
                            ></van-stepper>
                        </div>

                    </div>

                  </div>

            </div>
            <template slot="right">
              <button class="addCollection" type="button" @click="addCollection(message)">收藏</button>
              <button class="delectCart" type="button" @click="delectCart(item.shopCode,message.skuNo)">删除</button>
            </template>
            <div class="outSecksellBox" v-if="message.seckillFlag==4">
                <div class="outSecksellTitle"> 限{{message.seckillLimit}}件 </div>
                <div class="outSecksellMsg">秒杀商品限购{{message.seckillLimit}}件，超出将按原价购买该商品</div>
            </div>
          </van-swipe-cell>
        </div>
        <netWorkError v-if="ifNetWorkError"></netWorkError>
