        <img :src="img.noList" />
        <span>暂无商品</span>
      </div>
        <div ref="placeHolder" v-if="isShowPlaceHolder" style="height: 300px"></div>

      </div>


      <!--</div>-->
    <!-- </van-pull-refresh> -->
    <!--底部功能栏-->
